@import "../../styles/mixins/media.scss";

.aboutUsWrapper {
  margin-bottom: 60px;

  @include tabletAndDesktop {
    margin-bottom: 80px;
  }
}

.descriptionWrapper {
  background: linear-gradient(rgba(0, 0, 0, 1), rgba(0, 0, 0, 0));

  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;

  margin-bottom: 15px;
}

.description {
  font-size: 14px;
  line-height: 16px;

  @include tabletAndDesktop {
    font-size: 15px;
    line-height: 17px;
  }
}

.btnWrapper {
  display: flex;
  justify-content: center;
}

.btn {
  font-size: 15px;
  line-height: 17px;
  font-weight: 700;
  text-decoration: underline;
  background-color: transparent;
  border: none;
  color: var(--accent);

  &:hover,
  &:focus {
    text-decoration: none;
  }
}

.more {
  display: none;

  @include tabletAndDesktop {
    display: block;
  }
}

.partOfText {
  display: block;
  margin-bottom: 10px;
}
