@import "../../styles/mixins/media.scss";

.popularCategoriesWrapper {
  margin-bottom: 50px;

  @include tabletAndDesktop {
    margin-bottom: 70px;
  }
}

.catagoriesList {
  @include desktop {
    display: flex;
    gap: 15px;
  }
}

.titleWrapper {
  display: flex;
}

.link {
  position: relative;
  display: block;
  height: 180px;
  width: 100%;
  border-radius: 20px;
  margin-bottom: 16px;
  overflow: hidden;
  text-decoration: none;
  color: var(--white);

  @include tabletAndDesktop {
    margin: 0;
    height: 190px;
  }
}

.img {
  top: 0;
  right: 0;
  position: absolute;
  height: 200px;
  border-radius: 20px;
}

.largeImg {
  @include tablet {
    height: 335px;
  }
}
.text {
  position: absolute;
  bottom: 30px;
  left: 30px;
  font-size: 14px;
  line-height: 16px;
  font-weight: 400;
  padding: 10px;
  max-width: 162px;
  border-radius: 10px;
}

.upContainer {
  @include tabletAndDesktop {
    display: flex;
    gap: 15px;
    margin-bottom: 30px;
  }
}

.downContainer {
  @include tabletAndDesktop {
    display: flex;
    gap: 15px;
  }
}

.biggerLink {
  @include tablet {
    width: 339px;
  }

  @include desktop {
    width: 300px;
  }

  @include tabletAndDesktop {
    height: 410px;
  }
}

.lessLinkWrapper {
  @include tablet {
    width: 339px;
  }

  @include desktop {
    width: 300px;
  }

  @include tabletAndDesktop {
    height: 410px;
    gap: 30px;
    display: flex;
    flex-direction: column;
  }
}
