@import "../../styles/mixins/media.scss";

.heroWrapper {
  margin-top: 16px;
  margin-bottom: 56px;

  @include tabletAndDesktop {
    margin-top: 30px;
    margin-bottom: 76px;
  }

  @include desktop {
    margin-left: 30px;
  }
}

.imgWrapper {
  height: 260px;
  background-image: url("../../assets/images/hero/sliderImage1.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  border-radius: 20px;

  @include tablet {
    height: 435px;
  }

  @include largeTablet {
    height: 571px;
  }

  @include desktop {
    height: 568px;
  }
}

.slider {
  @include desktop {
    margin-left: 234px;
  }
}

.dots {
  position: absolute;
  display: flex !important;
  justify-content: center;
  bottom: 20px;
  margin: 0 auto;
  width: 100%;
}

.dots li:not(:last-child) {
  margin-right: 10px;
}

.dots li button {
  background-color: var(--white);
  color: var(--white);
  border: none;
  font-size: 0;
  width: 11px;
  height: 11px;
  border-radius: 50px;
}
